<template>
	<view class="content">
		<view class="mask" v-if="loading" style="background-color:#F5F5F5;margin-top:0!important;z-index:9!important;">
			<view class="loading13" style="position:absolute;margin-top:-50upx;">
				<view></view>
				<view></view>
				<view></view>
				<view></view>
				<view></view>
			</view>
		</view>
		
		<view class="head-content" :style="{ padding: toTop+'px 0 0 0' }">
			<view class="head">
				<view :class="payType == 1 ? 'ok' : ''" @click="selectType(1)">金币套餐</view>
				<view :class="payType == 2 ? 'ok' : ''" @click="selectType(2)">VIP套餐</view>
			</view>
		</view>
		<view class="head-top">
			<view class="username">{{userInfo.nickname}}</view>
			<view class="vipTime">{{vipmsg}}</view>
			<view class="myGold">账户金币：{{userInfo.corn}}</view>
		</view>
		<view class="center-content">
			<view class="center-title">请您选择以下服务套餐</view>
			<scroll-view class="center-scroll" scroll-x="true">
				<!--金币套餐-->
				<view 
				:class="packId==g.cornId?'center-info select':'center-info default'" 
				v-for="(g, gi) in goldPackage" 
				:key="gi" 
				v-if="payType==1" 
				@click="cut(g.cornId,g.money,g.corn,1)">
					<view class="center-name">{{g.title}}</view>
					<view class="center-number">{{g.corn}}个</view>
					<view class="center-money">￥{{g.money}}</view>
				</view>
				<!--VIP套餐-->
				<view 
				:class="packId==v.vipId?'center-info select':'center-info default'" 
				v-for="(v, vi) in vipPackage" 
				:key="vi" 
				v-if="payType==2"
				@click="cut(v.vipId,v.money,0,2)">
					<view class="center-name">{{v.title}}</view>
					<view class="center-number" v-if="v.time == 0">无任何限制</view>
					<view class="center-number" v-else>{{v.time}}天</view>
					<view class="center-money">￥{{v.money}}</view>
				</view>
				
			</scroll-view>
			<view class="custom" v-if="payType==1&&custom">
				<input class="input" type="number" :placeholder="'自定义充值金币，1元＝'+cornCal+'金币'" @input="customPay" maxlength="6"/>
			</view>
			<view style="height:150upx;"></view>
		</view>
		<view class="bottom-pay">
			<view class="money">{{moneyTig}}</view>
			<view class="pay" @click="pay">立即付款</view>
		</view>
		<view class="pay-list" v-if="payStatus">
			<view class="pay-content">
				<image src="../../static/imgs/delx.png" class="close" @click="closePay"></image>
				<view class="pay-title">请选择支付方式</view>
				
				<view class="pay-code-list" v-for="(p, i) in payList" :key="i" @click="createOrder(p.id, p.payCode, p.type)">
					<image class="pay-img" :src="p.payIcon"></image>
					<view class="pay-name">{{p.payName}}</view>
					<image class="pay-botton" src="../../static/imgs/no.png"></image>
				</view>
		
				<!-- <view class="pay-code-list">
					<view class="pay-name">暂无支付方式，请联系客服</view>
				</view> -->
			</view>
		</view>
		<view style="height:100%;width:100%;background:rgba(0,0,0,0.3);position:fixed;top:0;left:0;z-index:999;" v-if="isPayStatus">
			<view style="width:65%;position:absolute;top:30%;left:16.3%;text-align:center;font-size:15px;background:#FFFFFF;padding:30px 5px;border-radius:10px;">
				<image src="../../static/imgs/msg.png" style="width:200upx;height:200upx;"></image>
				<view style="color:#666666;font-size:13px;height:50upx;line-height:50upx;">如付款成功未到账，请联系平台客服</view>
				<view style="background:#D89720;color:#FFFFFF;width:200upx;height:70upx;line-height:70upx;margin:10px auto 0;border-radius:5px;" @click="closePayStatus">
					我知道了
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import api from "@/common/api.js";
var _self;
export default {
	data() {
		return {
			toTop: 27,
			userId: 0,
			userInfo: {},
			vipmsg: '',
			goldPackage: [],
			vipPackage: [],
			packId: 0,
			payType: 1,
			payMoney: 0,
			moneyTig: '支付金额：0元',
			cornCal: 1,
			payStatus: false,
			payList: [],
			gold: 0,
			isPayStatus: false,
			custom: false,
			
			loading: true,
			vid: 0,
		}
	},
	onLoad(e) {
		//console.log(e)
		_self = this;
		uni.showLoading({ title:'加载中...',mask:true });
		var mobile = uni.getSystemInfoSync();
		_self.toTop = mobile.statusBarHeight+45;
		// 是否登录
		var info = api.getLogins();
		if(info === false) {
			_self.skipUrl();
		}else{
			_self.userId = info.userId;
			_self.getUserInfo();
		}
		_self.getRecPackage();
		if(e.type==2) _self.selectType(e.type);
		//console.log(e);
		if(e.vid){
			//console.log(e.vid);
			_self.vid = e.vid;
		} 
	},
	onBackPress(e) {
		//console.log(e)
		if(_self.vid > 0){
			uni.setStorage({
			    key: 'vid',
			    data: _self.vid,
				success: ()=> {
					var param = {
						url: '/pages/index/index',
						t: 'tab'
					}
					api.jumpUrl('/pages/video/play?type='+JSON.stringify(param), 'new');
				},
			    fail: ()=> {
			        api.showToast('服务器连接失败，请检查网络是否正常', 3000);
			    }
			});
			return true;
		}
	},
	methods: {
		// 提交充值
		pay() {
			if(_self.payMoney==0 || _self.payMoney<0){
				_self.msgData('支付金额必须大于0元', 2000);
				return false;
			}
			if(_self.payType==2){
				if(_self.userInfo.is_permanent == 1){
					_self.msgData('您已是尊贵的永久VIP，无须升级', 2000);
					return false;
				}
			}
			uni.showLoading({ title:'处理中...',mask:true });
			_self.getPayList();
		},
		// 读取支付列表
		getPayList() {
			uni.request({
				url: api.apiData.getPayList,
				method: 'GET',
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					//console.log(e)
					var data = e.data;
					if(data.Code == 200){
						var payList = data.Data.list;
						if(payList.length > 0){
							_self.payStatus = true;
							_self.payList = payList;
							uni.hideLoading();
						}else{
							_self.msgData('暂无可用支付接口，请联系平台客服', 2000);
						}
					}else{
						_self.msgData(data.Msg, 2000);
					}
				},
				fail: () => {
					// 加载失败提示
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		closePay() {
			_self.payStatus = !_self.payStatus;
		},
		closePayStatus() {
			_self.getUserInfo();
			_self.isPayStatus = !_self.isPayStatus;
		},
		// 创建订单
		createOrder(pid, payCode, type) {
			if(!pid){
				_self.msgData('付款失败，请退出后重试', 2000);
				return false;
			}
			/* if(!type){
				_self.msgData('原生支付正在，开发中', 2000);
				return;
			} */
			uni.showLoading({ title:'正在创建订单...',mask:true });
			// 发起请求
			uni.request({
				url: api.apiData.createOrder,
				method: 'POST',
				data: {
					userId : _self.userId,  // 用户ID
					payId  : pid,			// 支付方式ID
					buyType: _self.payType, // 1金币套餐2VIP套餐
					payCode: payCode, 		// 方式渠道标识
					price  : _self.payMoney,// 支付金额
					gold   : _self.gold,    // 金币数
					packId : _self.packId    // 套餐ID
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e)=> {
					var data = e.data;
					_self.closePay();
					if(data.Code == 200){
						var orderSn = data.Data.order_sn;
						if(!orderSn){
							_self.msgData('订单创建失败，请检查网络是否正常', 2000);
							return false;
						}
						// 第三方 
						if(type){
							uni.hideLoading();
							api.jumpUrl(api.apiData.getPay+'/orderSn/'+orderSn, 'web');
							setTimeout(() => {
								_self.closePayStatus();
							}, 3000);
						}else{
							uni.showLoading({ title:'正在支付...',mask:true });
							//var orderInfo = JSON.stringify(data.Data.alipayStrSign);
							var orderInfo = data.Data.alipayStrSign;
							// alipay支付宝 wxpay微信
							uni.requestPayment({
								provider: data.Data.pay_channel,
								orderInfo: orderInfo, //微信、支付宝订单数据
								success: (res)=> {
									_self.getUserInfo();
									_self.msgData("订单已付款成功", 2000);
									//console.log('success:' + JSON.stringify(res));
									setTimeout(() => {
										_self.closePayStatus();
									}, 3000);
								},
								fail: (err)=> {
									console.log('fail:' + JSON.stringify(err));
									_self.msgData("付款失败，支付已取消", 2000);
									//_self.msgData(JSON.stringify(err), 2000);
								}
							});
							//console.log(orderInfo);
						}
					}else{
						_self.msgData(data.Msg, 2000);
					}
				},
				fail: () => {
					_self.closePay();
					_self.msgData("服务器连接失败，请检测网格是否正常", 5000);
				}
			});
		},
		// 自定义充值
		customPay(e) {
			var point = e.target.value;
			var money = point/_self.cornCal;
			_self.cut(0, money, point, 1);
		},
		// 切换vip或金币
		selectType(type) {
			//_self.payType = type;
			_self.cut(0, 0, 0, type);
		},
		// 选择套餐
		cut(tid, money, gold, type) {
			// 套餐ID
			_self.packId = tid;
			// 支付金额
			_self.payMoney = money;
			// 1为金币，2为VIP
			_self.payType = type;
			_self.moneyTig = '支付金额：'+money+'元';
			// 金币数
			_self.gold = gold;
			//console.log(gold)
		},
		// 读取套餐
		getRecPackage() {
			uni.request({
				url: api.apiData.getCharge,
				method: 'GET',
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					var data = e.data.Data;
					_self.goldPackage = data.corn;
					_self.vipPackage = data.vip;
					_self.cornCal = data.cornCal;
					_self.custom = data.custom;
					if(!data.is_recharge){
						api.showToast(data.recharge_text, 2000);
						setTimeout(()=>{
							api.jumpUrl('/pages/member/member', 'tab');
						}, 1500);
					}else{
						setTimeout(()=>{
							_self.loading = false;
						}, 500);
					}
					//console.log(e); 
				},
				fail: ()=> {
					// 加载失败提示
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		// 读取个人信息
		getUserInfo() {
			uni.request({
				url: api.apiData.getInfo+'/userId/'+_self.userId,
				method: 'GET',
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					_self.userInfo = e.data.Data;
					_self.userInfo.nickname = (_self.userInfo.nickname == '') ? _self.userInfo.username : _self.userInfo.nickname;
					if(_self.userInfo.is_permanent == 1){
						_self.vipmsg = "永久VIP";
					}else if(_self.userInfo.isVip){
						_self.vipmsg = "VIP将在 "+_self.userInfo.vipEndTime +" 到期";
					}else{
						_self.vipmsg = "普通会员，可升级为尊贵的VIP";
					}
					uni.hideLoading();
				},
				fail: () => {
					// 加载失败提示
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		// 跳转登录
		skipUrl(url = '/pages/login/login', type = 1) {
			if(type == 1){
				uni.reLaunch({ url: url });
			}else{
				uni.navigateTo({ url: url });
			}
		},
		// 提示
		msgData(title = '', times = 1000, style = 'none') {
			// 加载失败提示
			uni.showToast({
				icon: style,
				title: title,
				mask: true,
				duration: times
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		_self.getUserInfo();
		_self.getRecPackage();
		uni.stopPullDownRefresh();
	}
};
</script>
<style lang="scss" scoped>
	/deep/.uni-scroll-view::-webkit-scrollbar {
		/* 隐藏滚动条，但依旧具备可以滚动的功能 */
		display: none
	}
</style>
<style>
	@import url('@/components/loading/loading.css');
	.close{ width:30px;height:30px;float:right;right:4%;top:8px; }
	.pay-list{ width:100%;height:100%;position:fixed;bottom:0;background:rgba(0,0,0,0.5);z-index:99; }
	.pay-content{ width:100%;background:#FFFFFF;bottom:0;position:absolute;padding:0 0 10px; }
	.pay-title{ width:100%;height:50px;line-height:50px;font-size:18px;padding:0 5%;color:#666666;border-bottom:1px solid #E8E8E8; }
	.pay-code-list{ width:90%;margin:20px 5% 10px;height:30px;border-bottom:1px dashed #E2E2E2;padding:0 0 15px; }
	.pay-code-list view{ display:inline-block; }
	.pay-img{ width:30px;height:30px;float:left;margin-right:8px; }
	.pay-name{ height:30px;float:left;font-size:16px;line-height:30px;color:#666666; }
	.pay-botton{ width:24px;height:24px;float:right;margin-top:3px; }

	.select{ border:1px solid #FFB800;background:#F8F8F8; }
	.custom{ width:98%;height:40px;line-height:40px;margin-top:20px;color:#666666;border:1px solid #E8E8E8;border-radius:3px;padding:0 1%; }
	.custom input{ font-size:13px; }
	.input{ height:40px;line-height:40px;padding:0 10px; }
	.money{ color:#FFFFFF;padding:0 15px; }
	.pay{ color:#FFFFFF;background:#ff5e63;float:right;padding:0 15px;margin-right:15px; }
	.bottom-pay{ width:100%;background:#2C405A;margin-top:10px;position:fixed;bottom:0;font-size:15px;height:50px;line-height:50px; }
	.bottom-pay view{ display:inline-block; }
	.center-content{ width:90%;margin-top:0;padding:10px 5%; }
	.center-title{ padding:15px 2px;color:#666666;font-size:16px; }
	.center-scroll{ white-space:nowrap; }
	.center-info{
		display: inline-block;
		margin: 0 30upx 0 0;
		width: 43%;
		height: 145px;
		border-radius: 8px;
		//border: 2px solid #3F536E;
		//box-shadow: 0px 6px 6px 0px rgba(103, 103, 103, 0.5);
		text-align: center;
		padding: 20px 0 0 0;
		//background: #3F536E;
	}
	//.default{ background: -webkit-gradient(linear, left top, right top, from(rgba(0,0,0,0.3)), to(#3F536E)); }
	.default{ border:1px solid #E8E8E8; }
	.center-info view{ padding:5px 1%;font-size:16px; }
	.center-name{ color: #333333;border-bottom:1px solid #E8E8E8;width:80%;margin:0 auto;height:20px;line-height:20px; }
	.center-number{ color: #666666;width:98%;height:50px;line-height:50px; }
	.center-money{ color:#FFB800;font-weight:bold;width:80%;margin:auto;border:1px solid #E8E8E8;border-radius:50px; }
	.myGold{ margin-top:15px;background:rgba(103,103,103,0.8);padding:8px 0;font-size:15px; }
	.gold{ width:30px;height:30px; }
	.head-content{ 
		width: 100%;
		height: 350upx;
		background-image: linear-gradient(to right, rgba(255,12,208,0.8), rgba(254,67,101,0.8));
		clip-path: polygon(0% 0%,0% 100%,100% 65%,100% 0%);
	}
	.username{ font-size:17px; }
	.vipTime{ font-size:12px;margin-top:10px;background:rgba(255,94,99,0.8);display:inline-block;padding:5px 10px;border-radius:5px; }
	.head-top{ 
		width:70%;
		background: rgba(239,187,52,0.9);
		margin: -200upx auto 0;
		border-radius: 10px;
		z-index: 3;
		position: relative;
		color: #FFFFFF;
		padding: 20px 0;
		height: 120px;
		text-align: center;
		box-shadow: 0px 6px 6px 0px rgba(103, 103, 103, 0.8);
	}
	.content{ width:100%; }
	.head{ 
		background: rgba(218,171,57,0.6);
		text-align: center;
		padding: 13px 0;
		color: #FFFFFF;
		width: 80%;
		margin: auto;
		border-radius: 10px;
		box-shadow: 0px 4px 4px 0px rgba(103, 103, 103, 0.3);
		height: 50upx;
		line-height: 50upx;
	}
	.head view{ display:inline-block;width:100px;font-size:18px; }
	.ok{ border-bottom:2px solid #FFFFFF;padding-bottom:2px; }
</style>
